                <br/>
                <input type="checkbox" id="filter-produkte-xxx" value="1" v-model="xxx" disabled="true">
                <label for="filter-produkte-xxx">Schokolade / Süßwaren</label>
                <br/>
                <input type="checkbox" id="filter-produkte-xxx" value="1" v-model="xxx" disabled="true">
                <label for="filter-produkte-xxx">Wein / Spirituosen</label>
            </b-col>
            <b-col cols="12" md="auto">
                <div class="my-filter-heading">Umkreis:</div>
                Standort im Umkreis von
                <br/>
                |------x------------| 30 km (1 - 500 km)
                <br/>
                um ...Heppenheim... [Ändern]
