                    <td>{{ item.contact.city }}</td>
                    <td>{{ item.category }}</td>
                    <td><a href="#" v-on:click.prevent="showDetails(item)">Details</a> <!--<span v-if="isCurrentItem(item)">-></span>--></td>
                </tr>
                </tbody>
                </table>
